// Share Form
// ---------------------------------------

#share_url {}
.share-success, .share-error, .copy-error {
  margin-top: 1.5rem;
  display: none;
  &.active {
    display: block;
  }
}
.share-success {
  p {
    color: $success;
  }
}
.share-error, .copy-error {
  p {
    color: $alert;
  }
}
.share-form, .center-section .share-form {
  // font-size: 1.1rem;
  .part-url, input, select, .button, .buttons {
    display: inline-block;
    vertical-align: middle;
    margin: 0 0.2rem;
  }
  .buttons {
    margin: 0;
  }
  .button {
    margin-right: 0;
    margin-left: 0.4rem;
  }
  .part-url {
    color: $medium-gray_fade;
    font-size: 1.4rem;
    position: relative;
    top: 0.05rem;
    &:first-child {
      margin-left: 0;
    }
  }
  input.postcode {
    width: 160px;
    margin: 0;
  }
  select.category, input.category {
    width: 230px;
    margin: 0;
  }
  @include small {
    .part-url, input, select, .buttons {
      display: block;
      text-align: center;
      margin: 0;
    }
    .part-url {
      margin: 0.25rem 0;
    }
    .buttons {
      display: block;
      margin-top: 1rem;
    }
    input.postcode, select.category, input.category {
      position: relative;
      margin: 0 auto;
      width: 300px;
    }
  }
}
