
// Footer
// ---------------------------------------

.footer-holder {
	background: $w;
	padding-top: 6.25rem;
	@include medium {
		padding-top: 5.4rem;
	}
	@include small {
		padding-top: 4rem;
	}
	@media print {
		display: none;
	}
}
address {
	font-style: normal;
	@extend p;
}
footer {
	background: $medium-gray;
	padding: 1.75rem 0 1.6rem 0;
	color: $w;
	.copy-container {
		margin-top: 1.5rem;
		p, address {
			margin: 0;
		}
		address {
			margin-bottom: 0.5rem;
		}
	}
	p {
		color: $w;
	}
	a {
		color: $w;
		font-weight: normal;
		@include st {
			opacity: 0.65;
			color: $w;
		}
		&.button {
			@include st {
				opacity: 1;
			}
		}
	}
	ul {
		font-size: $d-fs;
		@extend %no-list;
		li {
			display: inline-block;
			margin-right: 1em;
			&:last-child {
				margin-right: 0;
			}
		}
	}
	@include medium {
		text-align: center;
		.copy-container {
			max-width: none;
		}
	}
	@include small {
		text-align: center;
		ul {
			font-size: $d-fs-s;
			li {
				display: block;
				margin: 0.25em 0;
			}
		}
	}
}




