
// Form Styles
// ---------------------------------------

label {
  font-size: 1rem;
  margin-bottom: 0.5rem;
  cursor: pointer;
  font-size: $d-fs;
  color: $medium-gray;
  line-height: 1.4;
  @include small {
    font-size: $d-fs-s;
  }
  span {
    color: $light-gray;
  }
}
.select2 {
  margin-bottom: 1rem;
  width: 100% !important;
  ul {
    @extend %no-list;
  }
  &.selecton {
    @extend %custom-select;
  }
}
.select2-results {
  font-size: $d-fs;
}
.select2-container--default .select2-selection--multiple .select2-selection__rendered {
  padding: 0.5rem 0.5rem;
  display: block;
}
.select2-container .select2-selection--multiple {
  min-height: 0;
}
.select2-container--default .select2-search--inline .select2-search__field {
  @extend %form-style;
  @extend %custom-select;
  font-size: $d-fs;
  margin: 0;
  border: 0;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  padding-top: 0.325rem;
  padding-bottom: 0.15rem;
}
.select2-container--default .select2-selection--multiple {
  border: 1px solid $light-gray;
  border-radius: $d-rad;
  // height: 57px !important;
}
.select2-container--default.select2-container--focus .select2-selection--multiple {
  border: 1px solid $brand;
}
.select2-container--default .select2-results__option--highlighted[aria-selected] {
  background: $light-gray_fade;
  color: $medium-gray;
}
.select2-container--default .select2-results__option[aria-selected=true] {
  background: $medium-gray;
  color: $w !important;
}
.select2-container--default .select2-results__option .select2-results__option, .select2-container--default .select2-results__option {
  padding: 0.75rem 0.8rem 0.55rem 1.25rem;
  color: $medium-gray;
}
.select2-container--default .select2-results__group {
  padding: 0.75rem 1rem 0.55rem 0.5rem;
  color: $dark-gray;
}
.select2-container--default .select2-selection--multiple .select2-selection__choice, .selected-cat {
  border-radius: $d-rad;
  background: $w;
  color: $dark-gray;
  border: 1px solid $medium-gray;
  // border: 0;
  // font-weight: bold;
  position: relative;
  font-size: $d-fs;
  padding: 0.475rem 0.6rem 0.325rem 0.6rem;
  margin: 0.2rem 0.2rem;
  padding-left: 2.4rem;
}
.select2-container--default .select2-selection--multiple .select2-selection__choice__remove, .selected-cat span.remove {
  // overflow: hidden;
  // width: 0px;
  @include fas(times);
  width: 1.6rem;
  height: 1.6rem;
  position: relative;
  color: $alert;
  background: $alert;
  position: absolute;
  cursor: pointer;
  top: 0.4rem;
  left: 0.35rem;
  border-radius: 100%;
  &:before {
    font-size: 1rem;
    color: $w;
    position: absolute;
    top: 50%;
    z-index: 2;
    left: 50%;
    @extend %txy;
  }
  @include st {
    background: $medium-gray;
    color: $medium-gray;
  }
}
.select2-container .select2-search--inline {
  // height: 57px !important;
}
.f-error {
  color: $w;
  font-size: $d-fs;
  background: $alert;
  margin-top: -1.5rem;
  margin-bottom: 1.35rem;
  padding: 0.8rem 1.2rem;
  border-radius: $d-rad*2;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  @include small {
    margin-top: -1.35rem;
    font-size: $d-fs-s;
  }
  &.all {
    border-radius: $d-rad*2;
    margin-top: 0;
    margin-bottom: 2rem;
    padding-top: 1.2rem;
    padding-left: 1.6rem;
    padding-right: 1.6rem;
    padding-bottom: 1.3rem;
    max-width: 100 - 34.25%;
    @include media(0, $search-break) {
      max-width: 60%;
    }
    @include small {
      max-width: none;
      padding: 1.2rem;
    }
  }
  ul {
    margin-top: 0.5rem;
    li {
      font-size: 1rem;
      color: $w;
      margin: 0.35rem 0;
      &:before {
        background: $w;
      }
      &:first-child {
        margin-top: 0;
      }
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}
.messages {
  background: $success;
  color: $w;
  font-size: 1.1rem;
  @include transition(max-height, 1s);
  max-height: 100px;
  @include clear;
  overflow: hidden;
  @include small {
    font-size: 1rem;
    max-height: 500px;
  }
  .columns {
    position: relative;
    padding-right: 250px;
    padding-top: 1.25rem;
    padding-bottom: 1.1rem;
    @include small {
      padding-right: 0;
      padding-top: 1.1rem;
      padding-bottom: 1rem;
    }
  }
  p {
    color: $w;
    display: block;
    margin: 0;
  }
  a {
    color: $w;
    float: right;
    font-family: $heading-font;
    font-size: $d-fs;
    position: absolute;
    right: rem-calc($gutter/2);
    top: 50%;
    transform: translateY(-50%);
    @include st {
      opacity: 0.65;
    }
    @include small {
      font-size: $d-fs-s;
      position: static;
      margin-top: 0.25rem;
      float: none;
      display: inline-block;
      transform: none;
      top: auto;
      right: auto;
      font-size: 1.25rem;
    }
  }
  &.success {
    background: $success;
  }
  &.alert {
    background: $alert;
  }
}
.form {
  margin: 1.5rem 0;
  .radio-list, .checkbox-list {
    margin-bottom: 1.35rem;
  }
  input[type=submit], button {
    // margin-top: 1rem;
    @include media(0, 480) {
      width: 100%;
    }
  }
  p {
    margin: 0;
  }
  &:first-child {
    margin-top: 0;
  }
  &:last-child {
    margin-bottom: 0;
  }
}
input {
  &[type=text], &[type=number], &[type=phone], &[type=email], &[type=password], &[type=url] {
    @extend %form-style;
    height: 2.99rem;
  }
}
textarea {
  @extend %form-style;
  height: 200px;
  // width: 100%;
}
select {
  @extend %form-style;
  @extend %custom-select;
  height: 2.95rem;
}
.has-error {
  select, textarea, input[type=text], input[type=phone], input[type=email], input[type=password], input[type=url] {
    border-color: $alert;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
  }
}
input[type=checkbox] {
  @extend %checkbox;
}
input[type=radio] {
  @extend %radio;
}
.date-input {
  position: relative;
  input[type=text] {
    padding-right:3rem;
    &[readonly] {
      background-color: $w;
      cursor: pointer;
    }
  }
  @include fa(calendar);
  &:before {
    position: absolute;
    color: $medium-gray;
    font-size: 1.5rem;
    right: 0.75rem;
    top: 50%;
    margin-top: -0.6rem;
  }
}
$date-highlight-color: $light-gray;
$date-highlight-border-color: $medium-gray;
$date-highlight-focus-color: $dark-gray;
.picker {
  &__box {
    border-radius: $d-rad;
    font-size: 1.4rem;
  }
  &__year {
    color: inherit;
    font-style: normal;
    font-size: 1rem;
  }
  &__month {
    margin:0;
  }
  &__weekday {
    font-weight: inherit;
  }
  &__day {
    font-weight: inherit;
    &--today {
      &:before {
        border-top-color: $secondary;
      }
    }
    &--infocus, &--outfocus {
      @include st {
        background-color: $date-highlight-color;
      }
    }
    &--selected {
      background-color: $date-highlight-color;
      color: $b;
      @include st {
        background-color: $date-highlight-color;
        color: $b;
      }
    }
    &--highlighted {
      background-color: $date-highlight-color;
      border-color: $date-highlight-border-color;
      @include st {
        background-color: $date-highlight-color;
      }
    }
  }
  &--focused {}
  &__nav {
    &--next {
      @include st {
        background-color: $date-highlight-color;
      }
    }
    &--prev {
      @include st {
        background-color: $date-highlight-color;
      }
    }
  }
  &__button {
    &--today, &--clear, &--close {
      color: $b;
      border-radius: 0;
      @include st {
        color: $b;
        background: $date-highlight-color;
        border-color: $date-highlight-color;
      }
    }
    &--today {
      &:before {
        border-top-color: $secondary;
      }
    }
    &--clear {
      &:before {
        border-top-color: $alert;
      }
    }
    &--close {
      &:before {
        border-top: $dark-gray;
      }
    }
  }
}

// Contact Form 7 Specific Styles
span.wpcf7-not-valid-tip {
  @extend %error-message;
}
div.wpcf7-response-output {
  &.wpcf7-validation-errors {
    @extend %error-message;
  }
  &.wpcf7-mail-sent-ok {
    @extend %success-message;
  }
}
.ajax-loader {
  display: none !important;
}